<template>
  <div
    v-clickout
    class=" drag-size award-content-show editor-award stamp"
    :ref="id + 'ref'"
    :class="setClass"
  >
    <div @mousedown="setDomSize($event, 1)" class="set-size left-top"></div>
    <div @mousedown="setDomSize($event, 2)" class="set-size top"></div>
    <div @mousedown="setDomSize($event, 3)" class="set-size right-top"></div>
    <div @mousedown="setDomSize($event, 4)" class="set-size right"></div>
    <div @mousedown="setDomSize($event, 5)" class="set-size right-bottom"></div>
    <div @mousedown="setDomSize($event, 6)" class="set-size bottom"></div>
    <div @mousedown="setDomSize($event, 7)" class="set-size left-bottom"></div>
    <div @mousedown="setDomSize($event, 8)" class="set-size left"></div>
    <div @click="toDeleteImg" v-if="deleteImg" class="delete-img">å é¤</div>
    <img
      :id="id"
      ref="dragImg"
      :src="value.url"
      @load="getImgWH"
      @mousedown="moveStamp"
      :height="value.h"
      :width="value.w"
      draggable="false"
    />
  </div>
</template>

<script>
export default {
  name: 'dragSize',
  props: {
    value: {
      type: Object,
      default: () => {},
    },
    id: {
      type: String,
      default: 'dragImg',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    deleteImg: {
      type: Boolean,
      default: false,
    },
    limitRangeId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      setClass: '',
      limitRange: {
        width: 0,
        height: 0,
        x: 0,
        y: 0,
      },
    }
  },
  methods: {
    setPosition() {
      this.$refs[this.id + 'ref'].style.left = this.value.x + 'px'
      this.$refs[this.id + 'ref'].style.top = this.value.y + 'px'
    },
    // å é¤å¾ç
    toDeleteImg() {
      this.$emit('on-delete-img')
    },
    /**
     * @description: æå¨å°ç« çæ¹æ³
     */
    moveStamp(element) {
      if (!this.disabled) {
        let mine = this.$refs[this.id + 'ref'].style
        this.setClass = 'stamp-size-style'
        // çå¬é¼ æ å·¦é®çæ¶é´
        if (element.button == 0) {
          // è·åé¼ æ ç¸å¯¹äºç¹å»åç´ çä½ç½®
          let ox = element.offsetX
          let oy = element.offsetY
          document.onmousemove = (e) => {
            // é¼ æ åæ 
            let mouseX = e.clientX
            let mouseY = e.clientY
            //é¼ æ æä¸å¹¶ç§»å¨çäºä»¶
            //ç¨é¼ æ çä½ç½®åå»é¼ æ ç¸å¯¹åç´ çä½ç½®  ååå»å¾ççä½ç½®ï¼å¾å°åç´ çä½ç½®
            let left = mouseX - ox - this.limitRange.x
            let top = mouseY - oy - this.limitRange.y
            this.value.w = element.target.offsetWidth
            this.value.h = element.target.offsetHeight

            //ç»å®åç´ ä½ç½®å¹¶éå¶èå´å¨å¾çå
            if (
              top >= 0 &&
              left >= 0 &&
              top + element.target.offsetHeight <= this.limitRange.height &&
              left + element.target.offsetWidth <= this.limitRange.width
            ) {
              this.value.y = top
              this.value.x = left
              mine.top = top + 'px'
              mine.left = left + 'px'
            }
            // è¿ééå¶èå´çåæ¶  å¯ä»¥è®©å¶å¨èå´å¤æ ¹æ®åçèå´ç§»å¨
            // å·¦ä¸è§
            if (top <= 0 && left <= 0) {
              this.value.y = 0
              this.value.x = 0
              mine.top = 0
              mine.left = 0
            } else if (top <= 0 && left >= 0) {
              this.value.y = 0
              this.value.x = left
              mine.top = 0
              mine.left = left + 'px'
            }
            // å·¦ä¸è§
            if (
              top <= 0 &&
              left + element.target.offsetWidth >= this.limitRange.width
            ) {
              this.value.y = 0
              this.value.x = this.limitRange.width - element.target.offsetWidth
              mine.top = 0
              mine.left =
                this.limitRange.width - element.target.offsetWidth + 'px'
            } else if (
              top + element.target.offsetHeight >= this.limitRange.height &&
              left + element.target.offsetWidth <= this.limitRange.width
            ) {
              this.value.y =
                this.limitRange.height - element.target.offsetHeight
              this.value.x = left
              mine.top =
                this.limitRange.height - element.target.offsetHeight + 'px'
              mine.left = left + 'px'
            }
            // å³ä¸è§
            if (
              top + element.target.offsetHeight >= this.limitRange.height &&
              left <= 0
            ) {
              this.value.y =
                this.limitRange.height - element.target.offsetHeight
              this.value.x = 0
              mine.top =
                this.limitRange.height - element.target.offsetHeight + 'px'
              mine.left = 0
            } else if (left <= 0 && top >= 0) {
              this.value.y = top
              this.value.x = 0
              mine.top = top + 'px'
              mine.left = 0
            } else if (
              left + element.target.offsetWidth >= this.limitRange.width &&
              top >= 0
            ) {
              this.value.y = top
              this.value.x = this.limitRange.width - element.target.offsetWidth
              mine.top = top + 'px'
              mine.left =
                this.limitRange.width - element.target.offsetWidth + 'px'
            }
            // å³ä¸è§
            if (
              top + element.target.offsetHeight >= this.limitRange.height &&
              left + element.target.offsetWidth >= this.limitRange.width
            ) {
              this.value.y =
                this.limitRange.height - element.target.offsetHeight
              this.value.x = this.limitRange.width - element.target.offsetWidth
              mine.top =
                this.limitRange.height - element.target.offsetHeight + 'px'
              mine.left =
                this.limitRange.width - element.target.offsetWidth + 'px'
            }
          }
          document.onmouseup = () => {
            this.setClass = ''
            document.onmousemove = null
            document.onmouseup = null
          }
        }
      }
    },
    /**
     * @description: type å¤æ­å½åæå¨çæ¯åªä¸ª
     * 1: å·¦ä¸   æ¹åå¾ççå®ä½
     * 2: æ­£ä¸   æ¹åå¾ççå®ä½
     * 3: å³ä¸   æ¹åå¾ççå®ä½
     * 4: å³
     * 5: å³ä¸
     * 6: ä¸
     * 7: å·¦ä¸   æ¹åå¾ççå®ä½
     * 8: å·¦   æ¹åå¾ççå®ä½
     */
    // è®¾ç½®å¤§å°
    setDomSize(element, type) {
      if (!this.disabled) {
        let mine = this.$refs[this.id + 'ref'].style
        // çå¬é¼ æ å·¦é®çäºä»¶
        if (element.button == 0) {
          // è·åé¼ æ ç¸å¯¹äºç¹å»åç´ çä½ç½®
          let ox = element.offsetX
          let oy = element.offsetY
          document.onmousemove = (e) => {
            // é¼ æ åæ 
            let mouseX = e.clientX
            let mouseY = e.clientY
            //é¼ æ æä¸å¹¶ç§»å¨çäºä»¶
            //ç¨é¼ æ çä½ç½®åå»é¼ æ ç¸å¯¹åç´ çä½ç½®  ååå»å¾ççä½ç½®ï¼å¾å°åç´ çä½ç½®
            let left = mouseX - ox - this.limitRange.x
            let top = mouseY - oy - this.limitRange.y

            switch (type) {
              case 1:
                // éå¶  å·¦ä¸è§ä¸è½æå°å¾ççå³ä¸è§ å¹¶ä¸å¨å¾çå
                if (
                  top < this.value.y + this.value.h - 30 &&
                  left < this.value.x + this.value.w - 30 &&
                  top >= 0 &&
                  left >= 0
                ) {
                  this.value.h += this.value.y - top
                  this.value.w += this.value.x - left
                  this.value.y = top
                  this.value.x = left
                  mine.top = top + 'px'
                  mine.left = left + 'px'
                }

                break
              case 2:
                if (top >= 0 && top < this.value.y + this.value.h - 30) {
                  this.value.h += this.value.y - top
                  this.value.y = top
                  mine.top = top + 'px'
                }
                break
              case 3:
                if (
                  top < this.value.y + this.value.h - 30 &&
                  left > this.value.x + 30 &&
                  top >= 0 &&
                  left <= this.limitRange.width
                ) {
                  this.value.h += this.value.y - top
                  this.value.w = left - this.value.x
                  this.value.y = top
                  mine.top = top + 'px'
                }
                break
              case 4:
                if (left <= this.limitRange.width && left > this.value.x + 30) {
                  this.value.w = left - this.value.x
                }
                break
              case 5:
                if (
                  left > this.value.x + 30 &&
                  top > this.value.y + 30 &&
                  top <= this.limitRange.height &&
                  left <= this.limitRange.width
                ) {
                  this.value.w = left - this.value.x
                  this.value.h = top - this.value.y
                }
                break
              case 6:
                if (top > this.value.y + 30 && top <= this.limitRange.height) {
                  this.value.h = top - this.value.y
                }
                break
              case 7:
                if (
                  top > this.value.y + 30 &&
                  top <= this.limitRange.height &&
                  left >= 0 &&
                  left < this.value.x + this.value.w - 30
                ) {
                  this.value.h = top - this.value.y
                  this.value.w -= left - this.value.x
                  this.value.x = left
                  mine.left = left + 'px'
                }
                break
              case 8:
                if (left >= 0 && left < this.value.x + this.value.w - 30) {
                  this.value.w -= left - this.value.x
                  this.value.x = left
                  mine.left = left + 'px'
                }
                break
            }
          }
          document.onmouseup = () => {
            this.setClass = ''
            document.onmousemove = null
            document.onmouseup = null
          }
        }
      }
    },
    // è·åå¾ççå®½é«
    getImgWH() {
      if (this.value.w == 0 || this.value.w == '') {
        const img = this.$refs.dragImg
        this.value.w = img.naturalWidth
        this.value.h = img.naturalHeight
      }
    },
    // æ ¹æ®ä¼ å¼è®¾ç½®æå¨çæä»¶èå´
    getParentDomLimitRange() {
      let dom = document.getElementById(this.limitRangeId)
      dom = dom ? dom : document.body
      dom.style.position = 'relative'
      // å¤æ­æ¯å¦æè½½å°æå®çèç¹
      dom.appendChild(this.$el)
      this.limitRange.width = dom.scrollWidth
      this.limitRange.height = dom.scrollHeight
      // è·åå½ådomå°è§å¾åå®¹çtop  left  å®èapi
      this.limitRange.x = dom.getBoundingClientRect().left
      this.limitRange.y = dom.getBoundingClientRect().top
    },
  },
  mounted() {
    this.getParentDomLimitRange()
    this.setPosition()
  },
  // éåºæ¶åç§»é¤
  destroyed() {
    if (this.limitRangeId && this.$el && this.$el.parentNode) {
      this.$el.parentNode.removeChild(this.$el)
    }
  },
  // èªå®ä¹æä»¤
  directives: {
    clickout: {
      bind(el) {
        function documentHandler(e) {
          let child = el.childNodes
          // è¿éå¤æ­ç¹å»çåç´ æ¯å¦æ¯æ¬èº«ï¼æ¯æ¬èº«ï¼åè¿å
          if (el.contains(e.target)) {
            child.forEach((res) => {
              //  æ§å¶8ä¸ªå­divæ¾ç¤ºéè
              if (
                res.classList.value.indexOf('set-size') > -1 ||
                res.classList.value.indexOf('delete-img') > -1
              ) {
                res.style.display = 'block'
              }
            })
            el.classList.add('stamp-size-style')
            return false
          } else {
            child.forEach((res) => {
              if (
                res.classList.value.indexOf('set-size') > -1 ||
                res.classList.value.indexOf('delete-img') > -1
              ) {
                res.style.display = 'none'
              }
            })
            el.classList.remove('stamp-size-style')
          }
        }
        // ç»å½ååç´ ç»å®ä¸ªç§æåéï¼æ¹ä¾¿å¨unbindä¸­å¯ä»¥è§£é¤äºä»¶çå¬
        el.__vueClickOutside__ = documentHandler
        document.addEventListener('click', documentHandler)
      },
      unbind(el) {
        // è§£é¤äºä»¶çå¬
        document.removeEventListener('click', el.__vueClickOutside__)
        delete el.__vueClickOutside__
      },
    },
  },
  watch: {
    limitRangeId: function() {
      this.getParentDomLimitRange()
    },
  },
}
</script>

<style lang="less" scoped>
.award-content-show {
  cursor: move;
  position: absolute;
  white-space: nowrap;
  user-select: none;
  text-align: left;
}
