    border: 1px solid #007aff;
    background: #fff;
  }
  .delete-img {
    display: none;
    position: absolute;
    cursor: pointer;
    bottom: 10px;
    right: 10px;
    color: #333;
    font-size: 12px;
    padding: 2px 10px;
    background: #efeff4;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
    border-radius: 4px;
  }
  .left-top {
    cursor: nwse-resize;
    top: -5px;
    left: -5px;
  }

  .top {
    cursor: ns-resize;
    top: -5px;
    left: 50%;
    margin-left: -5px;
  }

  .right-top {
    cursor: nesw-resize;
    top: -5px;
    right: -5px;
  }

  .right {
    cursor: ew-resize;
    top: 50%;
    margin-top: -5px;
    right: -5px;
  }

  .right-bottom {
    cursor: nwse-resize;
    bottom: -5px;
    right: -5px;
  }

  .bottom {
    cursor: ns-resize;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
  }

  .left-bottom {
    cursor: nesw-resize;
    bottom: -5px;
    left: -5px;
  }

  .left {
    cursor: ew-resize;
    top: 50%;
    margin-top: -5px;
    left: -5px;
  }
}
</style>
